body {
  margin: 0;
  padding: 0;
  background-color: #eeeeee;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.section{
  padding: 0 2em 2em 2em;
}

button:focus{
  background-color: #263238;
}

button:hover{
  background-color: #263238 ;
}

.MuiPrivateTabIndicator-colorSecondary-35 {
  background-color: #ad1457;
}

.resume-page{
  padding: 1em;
}

.main-panel{
  min-height: 50em; 
}

.sub-panel{
  padding-top: 5em;
}

.uni-logo{
  padding-top:10px;
}

.progress-bar{
  margin-top: 15px;
}

.skill-progress{
  background: #f3e5f5;
}

.project-card-title{
  font-size: 1.2rem !important;
}

@media (max-width: 400px) { 

  .contact-section{
    padding: 0;
  }
      
}

.contact-section{
  padding: 1em;
}
.intro-box{
  background-color: black;
  opacity: 0.7;
  border-radius: 25px;
}

.landing-page{
  min-height: 50em;
  background-image: url(/portfolio/static/media/rockclimb.5a0cb1c3.jpg);
  background-size: cover;
  background-position: top;
  padding: 0;
}

.sticky {
  position: fixed;
  z-index: 2;
}

.profile-img{
  object-fit: cover;
}

@media screen and (min-width: 1200px) {
  .flexbox {
    display: -webkit-flex;
    display: flex;
    overflow: hidden;
  }
}
blockquote {
  margin: 10px;
  padding-left: 1.0rem;
  border-left: 5px solid #42a5f5;
} 

@media screen and (min-width: 1200px){
  .shrink-img{
    width: 30%;
    height: 30%;
  }
}

.halign{
  position: relative;
  left:50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

@media screen and (min-width: 1200px){
  .valign{
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
}

.custom-bullets:before {
  content: '\29BF';
  margin-left: -21px;
  margin-right: 10px;
  color: #ad1457
}

.resume-link {
  background-color: #757575;
}

.resume-link:focus, .resume-link:hover {
  box-shadow: 0 0 0 2px #fff;
  background-color: #757575;
}
